@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.reset-default {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.page-link {
  text-decoration: none;
  color: #ffffff;
}

@media (max-width: 1023px) {
  .hidden-max-desktop {
    display: none;
  }
}

@media (min-width: 1024px) {
  .hidden-min-desktop {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-max-tablet {
    display: none;
  }
}

@media (min-width: 768px) {
  .hidden-min-tablet {
    display: none;
  }
}

@media (max-width: 767px) {
  [data-accordion-item].is-open [data-accordion-button]::before {
    display: none;
  }
  [data-accordion-item]:not(.is-open) [data-accordion-content] {
    display: none;
  }
}
[data-expandable]:not(.is-expanded) [data-expandable-item] {
  display: none;
}

[data-expandable].is-expanded [data-expandable-button-folded] {
  display: none;
}

[data-expandable]:not(.is-expanded) [data-expandable-button-expanded] {
  display: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1300px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}
@media (max-width: 1439px) {
  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 3;
}
@media (max-width: 1023px) {
  .header {
    position: absolute;
  }
}

.header__container {
  display: flex;
  padding-right: 60px;
}
@media (max-width: 1279px) {
  .header__container {
    padding: 0;
  }
}

.header__logo {
  display: block;
  padding: 30px 0 35px;
}
.header__logo:hover, .header__logo:focus {
  opacity: 0.8;
}
.header__logo:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .header__logo {
    padding: 20px 0 25px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    padding: 15px 0 20px;
    width: 35px;
    height: 35px;
  }
}

.header__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 160px;
  column-gap: 120px;
  flex-grow: 1;
}
@media (max-width: 1279px) {
  .header__list {
    padding-left: 80px;
    column-gap: 80px;
  }
}
@media (max-width: 1023px) {
  .header__list {
    justify-content: flex-end;
    column-gap: 0;
    padding-right: 10px;
  }
}
@media (max-width: 767px) {
  .header__list {
    padding-left: 0;
    padding-right: 0;
  }
}

.header__item {
  padding: 45px 0 55px;
}
@media (max-width: 1023px) {
  .header__item {
    padding: 0;
  }
}

.header__link {
  display: inline-flex;
  align-items: center;
  line-height: 18px;
}
@media (max-width: 1023px) {
  .header__link {
    padding: 30px 20px 50px;
  }
}
@media (max-width: 767px) {
  .header__link {
    padding: 20px 20px 30px;
  }
}
.header__link:hover, .header__link:focus {
  opacity: 0.8;
}
.header__link:active {
  opacity: 0.6;
}

.header__link-text {
  margin-left: 15px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #f2f5f6;
}

.header__btn {
  height: 40px;
  margin: 35px 0 45px;
  padding: 10px 15px;
  white-space: nowrap;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: #f2f5f6;
  text-transform: uppercase;
}
.header__btn:hover, .header__btn:focus {
  opacity: 0.8;
}
.header__btn:active {
  opacity: 0.6;
}

.major {
  position: relative;
  margin-bottom: 120px;
  padding: 0 0 20px;
  overflow: hidden;
  background: linear-gradient(180deg, rgba(4, 24, 39, 0.7) 71.72%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .major {
    margin-bottom: 80px;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
  }
}
@media (max-width: 767px) {
  .major {
    margin-bottom: 50px;
  }
}

.major__image {
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
  background-color: #041827;
}

.major__picture {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.major__content {
  padding: 304px 0 260px 125px;
}
@media (max-width: 1023px) {
  .major__content {
    padding: 180px 0 185px;
  }
}
@media (max-width: 767px) {
  .major__content {
    padding: 150px 0 170px;
  }
}

.major__content h2 {
  margin: 0 0 32px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  color: #f2f5f6;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .major__content h2 {
    margin-bottom: 0;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .major__content h2 {
    margin-bottom: 8px;
    font-size: 36px;
    line-height: 42px;
  }
}

.major__content p {
  max-width: 620px;
  margin: 0 0 47px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  text-transform: lowercase;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .major__content p {
    margin-bottom: 57px;
  }
}
@media (max-width: 767px) {
  .major__content p {
    margin-bottom: 46px;
    font-size: 14px;
    line-height: 21px;
  }
}

.major__link-button {
  margin: 0;
  padding: 15px 27px 19px;
  border: none;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  color: #f2f5f6;
  background-color: #00b2ff;
  white-space: nowrap;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .major__link-button {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .major__link-button {
    width: 100%;
    margin-left: 0;
    padding: 12px 37px;
    font-size: 14px;
    line-height: 16px;
  }
}
.major__link-button:hover, .major__link-button:focus-visible {
  opacity: 0.8;
}
.major__link-button:active {
  opacity: 0.6;
}

.advantages {
  margin-bottom: 119px;
}
@media (max-width: 1023px) {
  .advantages {
    margin-bottom: 75px;
  }
}
@media (max-width: 767px) {
  .advantages {
    margin-bottom: 46px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 31px;
