  font-family: "Roboto Condensed";
  margin: 0 0 14px;
  padding: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .advantages__item h3 {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .advantages__item h3 {
    margin-bottom: 6px;
    font-size: 20px;
  }
}
.advantages__item svg {
  display: block;
  margin-bottom: 21px;
  width: 35px;
  height: 35px;
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .advantages__item svg {
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .advantages__item svg {
    margin-bottom: 13px;
    width: 30px;
    height: 30px;
  }
}
.advantages__item p {
  margin: 0 0 10px;
  padding: 0;
  color: #041827;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
}
@media (max-width: 1023px) {
  .advantages__item p {
    line-height: 21.5px;
  }
}
@media (max-width: 767px) {
  .advantages__item p {
    font-size: 14px;
    line-height: 19px;
  }
}
.advantages__item:hover {
  border: 1px solid transparent;
  background-color: #00b2ff;
}
.advantages__item:hover h3,
.advantages__item:hover p {
  color: #f2f5f6;
}
.advantages__item:hover svg {
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .advantages__item {
    min-height: 360px;
    margin-left: 5px;
    padding: 39px 44px 10px 39px;
  }
  .advantages__item:nth-of-type(3n) h3 {
    margin-bottom: 5px;
  }
  .advantages__item:nth-of-type(4n) h3 {
    margin-bottom: 3px;
  }
  .advantages__item:nth-of-type(3n) p, .advantages__item:nth-of-type(4n) p {
    padding-bottom: 41px;
  }
}
@media (max-width: 767px) {
  .advantages__item {
    margin-left: 5px;
    padding: 29px 24px 10px;
  }
  .advantages__item:nth-of-type(n), .advantages__item:nth-of-type(2n) {
    min-height: 340px;
  }
  .advantages__item:nth-of-type(3n), .advantages__item:nth-of-type(4n) {
    min-height: 325px;
  }
  .advantages__item:nth-of-type(3n) svg, .advantages__item:nth-of-type(4n) svg {
    margin-bottom: 19px;
  }
  .advantages__item:nth-of-type(3n) h3, .advantages__item:nth-of-type(4n) h3 {
    line-height: 28px;
    margin-bottom: 13px;
  }
