.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 146px 40px 60px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 10px 0;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 37px 40px 44px;
  background-color: #00b2ff;
  border-radius: 3px;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  color: #ffffff;
  cursor: pointer;
}
.modal__close-btn:hover {
  opacity: 0.7;
}
.modal__close-btn:active {
  opacity: 0.3;
}
.modal__close-btn:focus-visible {
  opacity: 0.7;
  outline: #041827 auto 1px;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal h2 {
  margin: 0 0 4px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42.19px;
  color: #ffffff;
}

.modal p {
  margin: 0 0 39px;
  padding-right: 25px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21.5px;
  color: #ffffff;
}

.modal__form-container {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.modal__form-textarea {
  margin-bottom: 2px;
}

.modal__agreement {
  padding: 0 10px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 15.58px;
  color: #ffffff;
}
.modal__agreement span::before {
  top: 0;
}

.modal__btn {
  max-width: unset;
}

